.pokemonList{
    margin: 5px 0;
    padding: 0;
}
.pokemonList li{
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    padding: 5px 0 5px 5px;
    color: #ffffff;
    margin: 0 3px 5px 3px; 
    font-size: 0.8em;
    font-weight:600;
    background: #0314af;
    border: 1px solid #ffffff;
    border-bottom-right-radius: 15px;
    transition-delay: 0.5s;
    transition: 0.8s;
    position: relative;
}
  
.pokemonList li:hover{
    justify-content: center;
    background: #ffffff;
    border: 1px solid #ffffff;
    border-bottom-right-radius: 0;
    color: #030d63;
    margin: 0 0 5px 0;
    padding: 5px 0 5px 8px;
    transition: 0.7s;
    transition-delay: 0.07s;
}
.newIcon p{
    margin: 0;
    color: #f1bd5c;
    padding: 2px 4px 2px 4px;
    font-size: 0.8em;
    font-family:Arial, Helvetica, sans-serif;
    transition: 0.8s;
}
  
.pokemonList li:hover .newIcon p{
    color: #c70707;
    font-size: 1em;
    font-weight: bold;
    transition: 1s;
}